import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Form, Col, Modal, Table, message } from 'antd';
import { TableData, SelectAjax, YearSemesterInput } from 'components/common/index';
import { getQueryParams, udpateQueryParams } from 'utils/urlUtil';
import {
  getPolicyStudents,
  createPolicyStudents,
  deletePolicyStudents,
} from 'services/policyStudent';
import { getPolicyObjects } from 'services/policyObjectForPolicyStudent';
import { dateToString } from 'utils/formatUtil';
import { BtnSave, BtnCancel } from 'components/common/button/index';
import SearchBar from './SearchBar';
// import FormYearSemesterInput from './FormYearSemesterInput';
import './style.scss';

@Form.create({ name: 'formListPolicyObject' })
class ListPolicyObject extends Component {
  state = {
    reload: 0,
    paramSearch: {},
    visible: false,
    type: '',
    enableBtnCancelObject: true,
    hasRowSelected: '',
    dataSearchTable: [],
    dataForSetPolicyStudents: [],
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  setFieldsYearSemesterInput = () => {
    const { paramSearch } = this.state;
    const { form } = this.props;
    console.log(paramSearch.semesterId);
    console.log(paramSearch.yearEduId);
    form.getFieldDecorator('semesterId');
    form.getFieldDecorator('yearEduId');
    form.setFieldsValue({ semesterId: paramSearch.semesterId, yearEduId: paramSearch.yearEduId });
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  showModal = type => {
    this.setState(
      {
        visible: true,
        type,
      },
      () => {
        this.setFieldsYearSemesterInput();
      }
    );
  };

  hideModal = type => {
    this.setState({
      visible: false,
      type,
    });
    const { dataSearchTable } = this.state;
    const data = dataSearchTable.map(x => {
      const { studentId } = x;
      const { semesterId } = x;
      const { yearEduId } = x;
      const { policyObjectId } = x;
      return { studentId, semesterId, yearEduId, policyObjectId };
    });
    this.cancelDataPolicyStudents(data);
    this.setState({ dataSearchTable: [] });
    this.setState({ reload: this.state.reload + 1 });
    message.success('Xóa thành công');
  };

  cancelDataPolicyStudents = async data => {
    try {
      console.table(data);
      await deletePolicyStudents(data);
    } catch (error) {
      console.log(error);
    }
  };

  handleOk = () => {
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const { dataForSetPolicyStudents } = this.state;
      const data = dataForSetPolicyStudents.map(x => {
        const { studentId } = x;
        return { studentId, ...values };
      });
      if (data.length === 0) {
        message.success('Vui lòng chọn sinh viên!');
      }
      if (data.length > 0) {
        message.success('Thêm thành công!');
        this.saveDataPolicyStudents(data);
        this.setState({
          visible: false,
        });
        this.props.history.goBack();
        this.setState({ reload: this.state.reload + 1 });
        // this.setState({ dataSearchTable: [] });
      }
    });
  };

  saveDataPolicyStudents = async data => {
    try {
      console.table(data);
      await createPolicyStudents(data);
    } catch (error) {
      console.log(error);
    }
  };

  handleCancel = e => {
    e.preventDefault();
    this.setState({
      visible: false,
    });
  };

  render() {
    this.columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        render: (text, record) => dateToString(record.dob, 'DD/MM/YYYY'),
      },
      {
        title: 'Lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Tên đối tượng ',
        dataIndex: 'policyObjectLabel',
        key: 'policyObjectLabel',
      },
    ];

    const rowSelectedOfDataSearchTable = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({ dataSearchTable: selectedRows });

        const hasRowSelected = selectedRowKeys.length > 0;
        this.setState({ hasRowSelected });

        const policyId = selectedRows.map(x => {
          const { policyObjectId } = x;
          return policyObjectId;
        });

        if (policyId.length > 0) {
          for (let i = 0; i < policyId.length; i += 1) {
            if (policyId[i] !== null) {
              this.setState({ enableBtnCancelObject: false });
            } else if (policyId[i] === null) {
              this.setState({ enableBtnCancelObject: true });
            }
          }
        }
      },
    };

    const rowSelectedSetPolicyStudents = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({ dataForSetPolicyStudents: selectedRows });
      },
    };

    const { getFieldDecorator } = this.props.form;
    const { paramSearch = {} } = this.state;
    return (
      <Fragment>
        <Card title="Chọn đối tượng chính sách">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableData
                rowSelection={rowSelectedOfDataSearchTable}
                reload={this.state.reload}
                paramSearch={this.state.paramSearch}
                allowGetData={
                  !!paramSearch.trainTypeId &&
                  !!paramSearch.facultyId &&
                  !!paramSearch.courseId &&
                  !!paramSearch.semesterId &&
                  !!paramSearch.yearEduId
                }
                service={getPolicyStudents}
                columns={this.columns}
                rowKey="id"
              />
            </Col>
            <Col span={24} style={{ textAlign: 'center' }}>
              <div className="btn-group">
                <BtnSave
                  onClick={() => this.showModal('set')}
                  title="Xét đối tượng"
                  disabled={!this.state.hasRowSelected}
                />
                <BtnCancel
                  onClick={() => this.hideModal('unset')}
                  title="Hủy đối tượng"
                  disabled={this.state.enableBtnCancelObject}
                />
              </div>
            </Col>
          </Row>
          <Modal
            style={{ top: 20 }}
            width="80%"
            title={this.state.type === 'set' ? 'Xét đối tượng' : 'Hủy đối tượng'}
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            okText="Đồng ý"
            cancelText="Hủy"
          >
            <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
              <Col span={24}>
                {/* <FormYearSemesterInput /> */}
                <YearSemesterInput form={this.props.form} />
                <Form.Item label="ĐTCS">
                  {getFieldDecorator(`policyObjectId`, {
                    // initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<SelectAjax service={getPolicyObjects} />)}
                </Form.Item>
              </Col>
              <Col span={24}>
                <Table
                  rowSelection={rowSelectedSetPolicyStudents}
                  dataSource={this.state.dataSearchTable}
                  columns={this.columns}
                  rowKey="id"
                />
              </Col>
            </Form>
          </Modal>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListPolicyObject />
  </Page>
);



